import React, { useEffect, useState } from 'react';
import '../App.css';

// Importing images
import cursor from '../assets/cursor.png';
import bird from '../assets/bird.png';
import github from '../assets/github.png';
import linkedin from '../assets/linkedin.png';
import usa from '../assets/flag-usa.png';
import brasil from '../assets/flag-brasil.png';
import drawing from '../assets/drawing.png';
import web from '../assets/web.png';
import vector from '../assets/vector.png';
import settings from '../assets/settings.png';
import language_image from '../assets/language.png';
import main from '../assets/main art.png'

const Home = () => {
    const [language, setLanguage] = useState(0); // 0 is english, 1 is portuguese

    useEffect(() => {
        document.title = 'Laura Coura - Portfolio';
    });

  return (
    <div className="app">
      <header className="header">
        {
          (language === 0) ?
            <div className="header-left">
              <a href='/'>Home</a>
              <a href='/#about'>About</a>
              <a href='/portfolio'>Portfolio</a>
            </div>
          :
          <div className="header-left">
              <a href='/'>Início</a>
              <a href='/#about'>Sobre</a>
              <a href='/portfolio'>Portfolio</a>
            </div>
        }
        

        <div className="header-right">
          <a href='https://www.linkedin.com/in/laura-coura/'>LinkedIn</a>
          <a href='https://github.com/LauraCoura'>GitHub</a>

          <button onClick={() => {setLanguage(1)}} className='button-image'>
            <picture className='language-image'>
              <source media="(min-height: 20px)" srcset={brasil} className="language-image"/>
              <img src={brasil} className="language-image"/>
            </picture>
          </button>

          <button onClick={() => {setLanguage(0)}} className='button-image'>
            <picture className='language-image'>
              <source media="(min-height: 20px)" srcset={usa} className="language-image"/>
              <img src={usa} className="language-image"/>
            </picture>
          </button>
        </div>
      </header>

      {(language === 0) ? // - - - - - - - - - - - - - - - ENGLISH VERSION - - - - - - - - - - - - - - -
        <main className='main'>
        <div className='row-basic'>
          <div>
            <h1>Hello world! <br/> I'm Laura Coura, <br/> a front-end developer</h1>

            <div className='row-basic'>
              <h1>and graphic designer</h1> 
                
              <picture className='cursor'>
                <source media="(min-width: 40px)" srcset={cursor} className="blink-image"/>
                <img src={cursor} className="blink-image"/>
              </picture>
            </div>

            <article>
              With a degree in Computer Science, <br/> my focus is on graphic and game design <br/> + web and mobile development.
            </article>

            <article className='portfolio-call'>
              Want to check out my portfolio? :)
            </article>

            <button><a href='/portfolio'>PORTFOLIO</a></button>
          </div>

          <picture className='main-image'>
            <source media="(min-height: 500px)" srcset={main} className="main-image"/>
            <img src={main} className="main-image"/>
          </picture>
        </div>

        <div id='about'>
          <h1 className='h1-title'>About me</h1>

          <article>
          With a degree in Computer Science and a technical degree in Industrial Automation, my career is focused on web and mobile (front-end) development. However, I also have interest in other fields of Computer Science, such as Artificial Intelligence, Data Mining and Machine Learning, and in the field of Graphic Design. Below, you can check out my qualifications and abilities:
          </article>

          <div className='about-container'>
            <picture className='about-image'>
              <source media="(min-height: 100px)" srcset={web} className="about-image"/>
              <img src={web} className="about-image"/>
            </picture>

            <div className='vertical-line'></div>

            <article className='about-article'>
              Java, JavaScript, TypeScript, C, C++, C#, Python, Haskell <br/>
              Kotlin, React Native, Android Studio <br/>
              HTML, CSS, React
            </article>
          </div>

          <div className='about-container-right'>
            <article className='about-article-right'>
              Adobe Photoshop, Adobe XD, Adobe InDesign <br/>
              Figma, Canva, Clip Studio Paint
            </article>

            <div className='vertical-line-right'></div>

            <picture className='about-image'>
              <source media="(min-height: 100px)" srcset={vector} className="about-image"/>
              <img src={vector} className="about-image"/>
            </picture>
          </div>

          <div className='about-container'>
            <picture className='about-image'>
              <source media="(min-height: 100px)" srcset={settings} className="about-image"/>
              <img src={settings} className="about-image"/>
            </picture>

            <div className='vertical-line'></div>

            <article className='about-article'>
              Arduino, Git <br/>
              Sony VEGAS Pro, Unity, Blender
            </article>
          </div>

          <div className='about-container-right'>
            <article className='about-article-right'>
              Image and video editing <br/>
              Illustration/digital art <br/>
              UI/UX design
            </article>

            <div className='vertical-line-right'></div>

            <picture className='about-image'>
              <source media="(min-height: 100px)" srcset={drawing} className="about-image"/>
              <img src={drawing} className="about-image"/>
            </picture>
          </div>

          <div className='about-container'>
            <picture className='about-image'>
              <source media="(min-height: 100px)" srcset={language_image} className="about-image"/>
              <img src={language_image} className="about-image"/>
            </picture>

            <div className='vertical-line'></div>

            <article className='about-article'>
            Portuguese: native <br/>
            English: advanced <br/>
            Spanish: average <br/>
            Libras: basic <br/>
            </article>
          </div>

        </div>

        <div id='contact'>
          <h1 className='h1-title'>Contact</h1>

          <article>
          Want to talk to me? You can contact me through LinkedIn! But if you're more interested in checking out some of my codes, you can find it on my GitHub:
          </article>

          <div className='contact-container'>
            <div className='contact-item'>
            <picture className='contact-image'>
              <source media="(min-height: 70px)" srcset={github} className="contact-image"/>
              <img src={github} className="contact-image"/>
            </picture>
              <h3>GitHub</h3>
              <p>LauraCoura</p>
            </div>

            <div className='contact-item'>
            <picture className='contact-image'>
              <source media="(min-height: 70px)" srcset={linkedin} className="contact-image"/>
              <img src={linkedin} className="contact-image"/>
            </picture>
              <h3>LinkedIn</h3>
              <p>laura-coura</p>
            </div>
          </div>
        </div>
      </main>

        : // - - - - - - - - - - - - - - - PORTUGUESE VERSION - - - - - - - - - - - - - - -

        <main className='main'>
        <div className='row-basic'>
          <div>
            <h1>Olá mundo! <br/> Eu sou Laura Coura, <br/>uma desenvolvedora <br/> front-end e </h1>

            <div className='row-basic'>
              <h1>designer gráfica</h1> 
                
              <picture className='cursor'>
                <source media="(min-width: 40px)" srcset={cursor} className="blink-image"/>
                <img src={cursor} className="blink-image"/>
              </picture>
            </div>

            <article>
              Formada em Ciência da Computação, <br/> meu foco é em design gráfico e de jogos <br/> + desenvolvimento web e móvel.
            </article>

            <article className='portfolio-call'>
              Quer conferir meu portfolio? :)
            </article>

            <button><a href='/portfolio'>PORTFOLIO</a></button>
          </div>

          <picture className='main-image-pt'>
            <source media="(min-height: 500px)" srcset={main} className="main-image-pt"/>
            <img src={main} className="main-image-pt"/>
          </picture>
        </div>

        <div id='about'>
          <h1 className='h1-title'>Sobre mim</h1>

          <article>
          Formada em Ciência da Computação e técnica em Automação Industrial, minha carreira é voltada ao desenvolvimento (front-end) web e mobile. Entretanto, também tenho interesse em outras áreas de Computação, como Inteligência Artificial, Mineração de Dados e Machine Learning, e na área de Design Gráfico. Abaixo, você pode conferir as minhas competências e habilidades:
          </article>

          <div className='about-container'>
            <picture className='about-image'>
              <source media="(min-height: 100px)" srcset={web} className="about-image"/>
              <img src={web} className="about-image"/>
            </picture>

            <div className='vertical-line'></div>

            <article className='about-article'>
              Java, JavaScript, TypeScript, C, C++, C#, Python, Haskell <br/>
              Kotlin, React Native, Android Studio <br/>
              HTML, CSS, React
            </article>
          </div>

          <div className='about-container-right'>
            <article className='about-article-right'>
              Adobe Photoshop, Adobe XD, Adobe InDesign <br/>
              Figma, Canva, Clip Studio Paint
            </article>

            <div className='vertical-line-right'></div>

            <picture className='about-image'>
              <source media="(min-height: 100px)" srcset={vector} className="about-image"/>
              <img src={vector} className="about-image"/>
            </picture>
          </div>

          <div className='about-container'>
            <picture className='about-image'>
              <source media="(min-height: 100px)" srcset={settings} className="about-image"/>
              <img src={settings} className="about-image"/>
            </picture>

            <div className='vertical-line'></div>

            <article className='about-article'>
              Arduino, Git <br/>
              Sony VEGAS Pro, Unity, Blender
            </article>
          </div>

          <div className='about-container-right'>
            <article className='about-article-right'>
              Edição de vídeos e imagens <br/>
              Ilustração/arte digital <br/>
              Design UI/UX
            </article>

            <div className='vertical-line-right'></div>

            <picture className='about-image'>
              <source media="(min-height: 100px)" srcset={drawing} className="about-image"/>
              <img src={drawing} className="about-image"/>
            </picture>
          </div>

          <div className='about-container'>
            <picture className='about-image'>
              <source media="(min-height: 100px)" srcset={language_image} className="about-image"/>
              <img src={language_image} className="about-image"/>
            </picture>

            <div className='vertical-line'></div>

            <article className='about-article'>
            Português: língua materna <br/>
            Inglês: avançado <br/>
            Espanhol: intermediário <br/>
            Libras: básico <br/>
            </article>
          </div>

        </div>

        <div id='contact'>
          <h1 className='h1-title'>Contato</h1>

          <article>
          Quer falar comigo? Pode entrar em contato pelo LinkedIn! Mas se você estiver mais interessado em conferir alguns dos meus códigos, você pode encontrá-los no meu GitHub:
          </article>

          <div className='contact-container'>
            <div className='contact-item'>
            <picture className='contact-image'>
              <source media="(min-height: 70px)" srcset={github} className="contact-image"/>
              <img src={github} className="contact-image"/>
            </picture>
              <h3>GitHub</h3>
              <p>LauraCoura</p>
            </div>

            <div className='contact-item'>
            <picture className='contact-image'>
              <source media="(min-height: 70px)" srcset={linkedin} className="contact-image"/>
              <img src={linkedin} className="contact-image"/>
            </picture>
              <h3>LinkedIn</h3>
              <p>laura-coura</p>
            </div>
          </div>
        </div>
      </main>
        }

      <footer>
        <picture className='bird-image'>
          <source media="(min-height: 70px)" srcset={bird} className="bird-image"/>
          <img src={bird} className="bird-image"/>
        </picture>

        <p className='credits'>
          Icon credits: <a href="https://www.flaticon.com/free-icons/code" title="code icons">Icongeek26 </a> +
          <a href="https://www.flaticon.com/free-icons/graphic-design" title="graphic-design icons">xnimrodx </a> +
          <a href="https://www.flaticon.com/free-icons/settings" title="settings icons" >Flaticon</a> + 
          <a href="https://www.flaticon.com/free-icons/illustration" title="illustration icons">Darius Dan</a> +
          <a href="https://www.flaticon.com/free-icons/extensible-markup-language" title="extensible markup language icons">Culmbio</a>
        </p>
      </footer>
    </div>
  );
}

export default Home;